.calculator {
  width: 400px;
  margin: 20px auto;
  padding: 15px;
  background: #222;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  color: white;
  text-align: center;
  margin-top: 60px;
}

.calculator .display {
  background: black;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  text-align: right;
}

.calculator .input {
  font-size: 1.4em;
  min-height: 25px;
}

.calculator .result {
  font-size: 1.6em;
  font-weight: bold;
  color: #0f0;
  min-height: 30px;
}

.calculator .advancedButtons {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 8px;
  margin-bottom: 15px;
}

.calculator .basicButtons {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
  margin-top: 15px;
}

.calculator .btn {
  font-size: 1.2em;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background: #444;
  color: white;
  cursor: pointer;
}

.calculator .btn:hover {
  background: #666;
}

.calculator .operator {
  background: #ff9500;
}

.calculator .operator:hover {
  background: #e68a00;
}

.calculator .equal {
  background: #0a74da;
}

.calculator .equal:hover {
  background: #095bb5;
}

.calculator .clear {
  background: #d32f2f;
}

.calculator .clear:hover {
  background: #b71c1c;
}

.calculator .delete {
  background: #6c757d;
}

.calculator .delete:hover {
  background: #5a6268;
}
